import { CSVLink, CSVDownload } from "react-csv";
import {
  MuiPickersUtilsProvider 
} from '@material-ui/pickers';
import {Grid} from '@material-ui/core';
import TextField from '@material-ui/core/TextField'
import React,{useState} from "react";
import Box from '@material-ui/core/Box';
import Button2 from './Button'

export default function Report(){

  var [csvData,setcsvData] = useState([])
  var [csvData18,setcsvData18] = useState([])
  const [gen,SetGen] = useState(false)
  const [FromDate, setFromDate] = useState('2021-01-01');
  const [ToDate, setToDate] = useState('2021-01-01');

  const setFromDateFunc = (event) =>{ 
    setFromDate(event.target.value);        
  }
  const setToDateFunc = (event) =>{ 
    setToDate(event.target.value);    
  }

  const handleClick = () =>{

    console.log("ok")
    let myHeaders = new Headers();
    let token = 'Bearer '+ sessionStorage.getItem('token')
  
   
    myHeaders.append("Authorization", token);

    let requestOptions = {
      method: 'GET',
      headers: myHeaders,
      redirect: 'follow'
    };
    
    let url = process.env.REACT_APP_API_SERVER_ADDRESS + "/vipreceiptheaders/genreport?fromdate=" + FromDate + "&todate=" +  ToDate;
    
    fetch( url , requestOptions)
      .then(response => response.text())
      .then(result =>{
     
        csvData = JSON.parse(result)       
        setcsvData(csvData)
      
      })
      .catch(error => console.log('error', error));

      url =  process.env.REACT_APP_API_SERVER_ADDRESS + "/vipreceiptheaders/genreport18?fromdate=" + FromDate + "&todate=" +  ToDate;
     
      fetch( url , requestOptions)
        .then(response => response.text())
        .then(result =>{
          
          csvData = JSON.parse(result)         
          setcsvData18(csvData)
        
        })
        .catch(error => console.log('error', error));
    

    SetGen(true)

  }  
  const goBack = () =>{
      SetGen(false);
  }
  const isAdmin = ()=>{
    
    if (sessionStorage.getItem('username')=='hradmin')
        return true
    return false

  }

  if (isAdmin()){
  return (
    <div>
      <Box display="flex" justifyContent="center" m={1} p={1} bgcolor="transparent">
        <TextField
                id="FromDate"
                label="From Date"
                type="date"
                onChange = {setFromDateFunc}
                defaultValue="2021-01-01"
                style={{ width: 220 }}
                InputLabelProps={{
                  shrink: true,
                }}
        />
        <TextField
                id="ToDate"
                label="To Date"
                type="date"
                onChange = {setToDateFunc}
                defaultValue="2021-01-01"
                style={{ width: 220 }}
                InputLabelProps={{
                  shrink: true,
                }}
        />
      </Box>
    <div>
    {
      gen ?  
      <div>
        <Box display="flex" justifyContent="center" m={1} p={1} bgcolor="transparent">
          <ul>
          <li><CSVLink data={csvData}>Frequency</CSVLink> </li>
          <li><CSVLink data={csvData18}></CSVLink> </li> 
          <button onClick={goBack}>Go Back</button>        
          </ul>
          
          
        </Box>
      </div>
        :
        <Box display="flex" justifyContent="center" m={1} p={1} bgcolor="transparent">
          <Button2 handleClick={handleClick}>click me</Button2>        
        </Box>
        
           
    } 
    </div>
    </div>
    )
  }
  
  return <div>You are not admin.</div>;
}