import React, { Component } from "react";
import * as Survey from "survey-react";
import "survey-react/modern.css";
import Login from '../../Login/Login';
import SurveyTemplate from './SurveyTemplate';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { withStyles  } from '@material-ui/core/styles';
import SurveyItem from './FoodSurvey'
import Box from '@material-ui/core/Box';
import './Survey.css';



Survey.StylesManager.applyTheme("modern");


  
function getToken() {
    const tokenString = sessionStorage.getItem('token');

  
    return tokenString;
  }


  
const useStyles = theme => ({
    root: {
      width: '100%',
    },
    heading: {
      fontSize: theme.typography.pxToRem(15),
      fontWeight: theme.typography.fontWeightRegular,
    },
    accordion :{
      width: '80%'
      
    }
  });

class SurveyComponent extends Component {
    
    constructor(props) {
        super(props);
    
        this.state = {
          ReceiptItemDrink: [],
          ReceiptItem: [ ],
          test:"test",
          category:"",
          expanded:'totalService',
          OverallComment:true,
         // FoodComment:true
        }

    }

    componentDidMount()
    {
        
      var myHeaders = new Headers();
      let token = 'Bearer '+ sessionStorage.getItem('token')
    
      
      myHeaders.append("Authorization", token);



      if(this.props.location.state.IsFeekbackFilled){   

        var raw = JSON.stringify({"IsRead":true});

        let requestOptions = {
          method: 'PUT',
          headers: myHeaders,
          body: raw,
          redirect: 'follow'
        };

        let url =  process.env.REACT_APP_API_SERVER_ADDRESS+'/Vipreceiptheaders/' + this.props.location.state.id
        fetch(url, requestOptions)
          .then(response => response.text())
          .then(result => console.log(result))
          .catch(error => console.log('error', error));

      }
      
      let requestOptions = {
          method: 'GET',
          redirect: 'follow',
          headers: myHeaders
        };
        
      let url =  process.env.REACT_APP_API_SERVER_ADDRESS+"/Vipreceiptdetails?ReceiptCode=" + this.props.location.state.receiptCode 
      console.log(url)
      fetch( url , requestOptions)
        .then(response => response.text())
        .then(result => {
            
            let jsonResult = JSON.parse(result)          
            console.log(jsonResult)      
            
            for(var i=0; i<jsonResult.length;i++){
                console.log(jsonResult[i].ClassName1)  
                if (jsonResult[i].Comment == null){  
                  if(jsonResult[i].ItemCode[0]=='9'){
                      this.state.ReceiptItemDrink.push(
                        {
                          itemname: jsonResult[i].ItemName2,
                          id: jsonResult[i].id,
                          category: jsonResult[i].ClassName1,
                        }
                      )
                  }
                  else{
                      this.state.ReceiptItem.push(
                        {
                          itemname: jsonResult[i].ItemName2,
                          id: jsonResult[i].id,
                          category: jsonResult[i].ClassName1,
                        })
                  }
                }

            }
            console.log(this.state.ReceiptItem)
            this.setState(state=>({
              ReceiptItem: state.ReceiptItem
            }))
            
            
        })
        .catch(error => console.log('error', error));

      url =  process.env.REACT_APP_API_SERVER_ADDRESS+"/Vipreceiptheaders?ReceiptCode=" + this.props.location.state.receiptCode
      fetch( url , requestOptions)
        .then(response => response.text())
        .then(result => {
          let jsonResult = JSON.parse(result)
          let arr=[]
          for(var i=0; i<jsonResult.length;i++){
              console.log(jsonResult[i])
              if (jsonResult[i].OverallComment != null){
                  this.setState( {expanded:'false',OverallComment:false});
              }
              // if (jsonResult[i].FoodComment != null)               
              //   this.setState( {FoodComment:false});
          }
      })
      .catch(error => console.log('error', error));
      
      // console.log(this.props.location.state.id)
      // console.log(this.state.expanded)
      // console.log(SurveyTemplate)
      
    }


    render() { 

        const { classes } = this.props;        
        
   
        const handleChange = (panel) => (event, newExpanded) => {
      
          this.setState(newExpanded ? {expanded:'totalService'} : {expanded:'false'});
        };

        const that = this;

        const json = SurveyTemplate.overallSurvey;
        const survey = new Survey.Model(json);     
        survey.onUploadFiles
        .add(function (survey, options) {
            console.log('trigger')
            var formData = new FormData();
            options
                .files
                .forEach(function (file) {
                    console.log(that.props.location.state.id)
                    formData.append('files', file);
                    formData.append('refId', that.props.location.state.id);
                    formData.append('ref', 'vipreceiptheader');
                    formData.append('field', 'Photo');
                  
                });
            var xhr = new XMLHttpRequest();

         
            xhr.open("POST",   process.env.REACT_APP_API_SERVER_ADDRESS + "/upload"); // https://surveyjs.io/api/MySurveys/uploadFiles
            xhr.setRequestHeader('Authorization', 'Bearer ' + sessionStorage.getItem('token'));
            xhr.onload = function () {
               
                let response = JSON.parse(xhr.response)[0]
              
                let url =  process.env.REACT_APP_API_SERVER_ADDRESS+response.url
                
                options.callback("success", options.files.map(file => {                    
                                     return {
                                         file: file ,
                                         type: file.type,
                                         content: url
                                     };
                                 }));

            };

            xhr.send(formData);
            console.log('photo sent')
            
            // setTimeout(()=>{
            //     options.callback("success", [
            //     {
            //     file: options.files[0]
            //     }
            //     ]);
            //     }, 2000);
                
                
        });

        survey
            .onComplete
            .add(function (result) {
              console.log('Completed')
                
              var myHeaders = new Headers();
              myHeaders.append("Content-Type", "application/json");
              let token = 'Bearer '+ sessionStorage.getItem('token')          
              
              myHeaders.append("Authorization", token);

            
              var comment = '{  "OverallComment": ' + JSON.stringify(result.data) +'}'
            
              var requestOptions = {
                method: 'PUT',
                headers: myHeaders,
                body: comment,
                redirect: 'follow'
              };
            
            
              let url  =  process.env.REACT_APP_API_SERVER_ADDRESS + "/Vipreceiptheaders/" + that.props.location.state.id
              

              fetch(url , requestOptions)
                .then(response => response.text())
                .then(result =>{
                  //  if(!that.state.FoodComment){
                      var myHeaders = new Headers();
                      var token = 'Bearer '+ sessionStorage.getItem('token')
                      myHeaders.append("Content-Type", "application/json");
                      console.log(token)
                      
                      myHeaders.append("Authorization", token);

                      var requestOptions = {
                          headers: myHeaders,
                          method: 'GET',
                          redirect: 'follow'
                      };
                  
                      
                      let allDone = false;
                      console.log(url)
                      //level2
                      fetch(url , requestOptions)
                      .then(response => response.text())
                      .then(result => {
                          var jsonResult = JSON.parse(result)
                          console.log(jsonResult)
                            
                    
                          for(let b of jsonResult.vipreceiptdetails){
                              console.log(b.Comment)
                              if(b.Comment){                   
                                  allDone = true
                              }else{
                                allDone=false
                                break
                              }
                          }
                          

                          console.log(allDone)

                      if (allDone ){                  
                      
                          var IsFeekbackFilled = '{  "IsFeekbackFilled": true}'                       
                      
                          var requestOptions = {
                          method: 'PUT',
                          headers: myHeaders,
                          body: IsFeekbackFilled,
                          redirect: 'follow'
                          };
                          
                          
                          
                          
                          //level3
                          fetch(url , requestOptions)
                          .then(response => response.text())
                          .then(result => console.log(result))
                          .catch(error => console.log('error', error));
                      }



                          })
                      .catch(error => console.log('error', error));
                      // }
          
                })
                .catch(error => console.log('error', error));
                  // document
                  //     .querySelector('#surveyResult')
                  //     .textContent = "Result JSON:\n" + JSON.stringify(result.data, null, 3);
                  that.setState( {expanded:'false',OverallComment:false});
                  
                }           
            );
          
        
          const handleCallback = (index) =>{
           
            let a = this.state.ReceiptItem
            a.splice(index,1)
            this.setState(state=>({                
              ReceiptItem: a
            }))
          
            // this.setState({data: childData})
          }

        return ( getToken()?
            <div>
                <Box p={3} >
                 <Typography  variant="h5"  style={{ fontWeight: 600 }}>  {!this.state.OverallComment /*&& !this.state.FoodComment*/ ? '()' : null }</Typography>
                </Box>

                { this.state.OverallComment ? <Accordion expanded={this.state.expanded === 'totalService'} onChange={handleChange('totalService')} >
                  <AccordionSummary
                  expandIcon={<ExpandMoreIcon />}
                  aria-controls="panel1a-content"
                  id="panel1a-header"
                
                  >
                    
                  </AccordionSummary>
                    <AccordionDetails>
                    <div>                
              
                            <div>
                                <Survey.Survey
                                model={survey}
                                    />      
                            </div>       
                            <div id='hidden'>________________________________________________________________________________________________________________________________________________</div>  
                        
                    </div>
                    
                    </AccordionDetails>
                  
                </Accordion> 
                
                : <div></div>}

                {/* { this.state.FoodComment ? 
                <Accordion >
                  <AccordionSummary
                  expandIcon={<ExpandMoreIcon />}
                  aria-controls="panel1a-content"
                  id="panel1a-header"
                  >
                    
                  </AccordionSummary>
                    <AccordionDetails>
                      <Survey.Survey
                        model={totalFoodComment}
                        />
                    </AccordionDetails>
                
                </Accordion> : <div></div>} */}
                    
                
                <Box p={3}>
                 <Typography  variant="h5"  style={{ fontWeight: 600 }}> </Typography>
                </Box>
                {this.state.ReceiptItem.map((v,i)=>{
                  return(
                    <div>
                      
                    <Accordion >
                        <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="panel1a-header"
                        >
                          <Typography className={classes.heading}>{v.itemname + '(' +v.id +')'}</Typography>
                        </AccordionSummary>
                        <AccordionDetails>
                          <SurveyItem headerid={this.props.location.state.id} index={i} 
                          category={v.category} id={v.id} receiptCode = {this.props.location.state.receiptCode} parentCallback = {handleCallback}
                          /* OverallFoodDone={!this.state.FoodComment} */ OverallServiceDone={!this.state.OverallComment}
                          />
                        </AccordionDetails>
                    </Accordion>
                    </div>
                  )
                })}

                <Box p={3}>
                 <Typography  variant="h5"  style={{ fontWeight: 600 }}> </Typography>
                </Box>
                {this.state.ReceiptItemDrink.map((v,i)=>{
                  return(
                    <div>
                    <Accordion >
                        <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="panel1a-header"
                        >
                          <Typography className={classes.heading}>{v.itemname + '(' +v.id +')'}</Typography>
                        </AccordionSummary>
                        <AccordionDetails>
                          <SurveyItem headerid={this.props.location.state.id} index={i} 
                          category={v.category} id={v.id} receiptCode = {this.props.location.state.receiptCode} parentCallback = {handleCallback}
                          /* OverallFoodDone={!this.state.FoodComment} */ OverallServiceDone={!this.state.OverallComment}/>
                        </AccordionDetails>
                    </Accordion>
                    </div>
                  )
                })}

               
              
            </div> : <Login/>);
        
    }

    
}


export default withStyles(useStyles)(SurveyComponent);
