import jsPDF from "jspdf";
import SurveyTemplate from './SurveyTemplate'
import chi from '../../../genjyuugothic/GenJyuuGothic-Light.ttf'
import chiBold from '../../../genjyuugothic/GenJyuuGothic-Bold.ttf'
import { ISO8601ToDate, ISO8601ToTime } from './ISOFormatter'


function getKeyByValue(object, value) {
  return Object.keys(object).find(key => object[key] === value);
}

const ResultTable = new Map()
ResultTable.set('1',{'question':"1.(10) ","weight":0.1 ,"total":10 })
ResultTable.set('2',{'question':"2.(6) ","weight":0.1,"total":6})
ResultTable.set('3',{'question':"3. (12) ","weight":0.1,"total":12})
ResultTable.set('4',{'question':"4. (20) ","weight":0.1,"total":20})
ResultTable.set('5',{'question':"5.(8) ","weight":0.05,"total":8})
ResultTable.set('6',{'question':"6. (22) ","weight":0.2,"total":22})
ResultTable.set('7',{'question':"7. (8) ","weight":0.05,"total":8})
ResultTable.set('p2',{'question':"8. (12)  ","weight":0.3,"total":12})

const overallq9to11 = new Map()
overallq9to11.set('9',": ")
overallq9to11.set('10',": ")
overallq9to11.set('11',": ")



export default function exportPDF(props){

  const unit = "pt";
  const size = "A4"; // Use A1, A2, A3 or A4
  const orientation = "portrait"; // portrait or landscape
  var Score = []
  

  const marginLeft = 40;
  var doc = new jsPDF(orientation, unit, size);

  doc.setFontSize(15);
  const myFont =  // load the *.ttf font file as binary string


  doc.addFont(chi, 'GenJyuuGothic-Light', 'normal');
  doc.addFont(chiBold, 'GenJyuuGothic-Bold', 'normal');
  doc.setFont('GenJyuuGothic-Light');
  const filename = props.OutletName1 + ' ('+props.OutletCode+')'+ '   ' + ISO8601ToDate(props.OpenTime) + ISO8601ToTime(props.OpenTime) ;
  const title = props.OutletName1 + ' ('+props.OutletCode+')'+ '   ' + ISO8601ToDate(props.OpenTime) + '   :'+ ISO8601ToTime(props.OpenTime) ;

  var headers = [["", ""]];
  
  let res = new Map()
  let firsttime = true
  doc.text(title, marginLeft, 40);
  
  
  for(let [k,v] of Object.entries(props.OverallComment)){
    let questionNumber = k.split('n')[1]    
    if (parseInt(questionNumber)<=8){
    res = new Map()
        
    for(let [kk,vv] of Object.entries(v)){
    
      if(kk[0]=='q'){
        let q = kk.split('_')
     
        if ( res.has(q[1])){
     
          let tmp = res.get(q[1])
          tmp['ans'] = vv
          res.set(q[1], tmp)
         
        }
        else{
          res.set(q[1],{'ans':vv})
        }

      }else{
        let splitBeforeDot = kk.split('.')
        if (res.has(splitBeforeDot[0])){
          let tmp = res.get(splitBeforeDot[0])
          tmp['question'] = kk
     
          res.set(splitBeforeDot[0], tmp)
        }
        else{
          
          res.set(splitBeforeDot[0],{'question':kk})
        }

      }

            
    }
    let t = {
      res: [  
      ]
    }
    let total =0 ;    
    var mapAsc = new Map(
      [...res.entries()].sort(function(a,b){
      return parseInt(a[0])-parseInt(b[0])
    }
    ));
    
    for(let [k,v] of mapAsc){
      
      t.res.push({question:v.question , mark: v.ans})
      total += parseInt(v.ans)

    }
    Score.push(total)
    t.res.push({question:"", mark: total})
    
    
    const data = t.res.map(elt=> [elt.question, elt.mark]);
     
    let y = 50
    if(firsttime){
      y = 50
      firsttime = false
    }
    else
      y = doc.lastAutoTable.finalY
    let content = {
      startY: y+ 20,     
      head: headers,
      body: data,
      styles: { font: 'GenJyuuGothic-Light' },
      didParseCell : function(data) {      
        if (data.row.index == t.res.length-1 )
          data.cell.styles.font  = 'GenJyuuGothic-Bold'
      },
      
      columnStyles: {
        0: {
          cellWidth: 'auto'
        },
        1: {
          cellWidth: 50
        }
      }
      
    };
     
    
  
    doc.autoTable(content);
  }
   
  }

  doc.addPage();
  let overallCommentContent = '';

  if(props.OverallComment.question9)
    overallCommentContent +=(overallq9to11.get("9")+ props.OverallComment.question9);
  else
    overallCommentContent +=(overallq9to11.get("9")+ "" );

  overallCommentContent +='\n\n'
  if(props.OverallComment.question10)
    overallCommentContent +=(overallq9to11.get("10")+ props.OverallComment.question10);
  else
    overallCommentContent +=(overallq9to11.get("10")+ "");

  overallCommentContent +='\n\n'
  if(props.OverallComment.question11)
    overallCommentContent +=(overallq9to11.get("11")+ props.OverallComment.question11);
  else
    overallCommentContent +=(overallq9to11.get("11")+ "");


  let splitoverallCommentContent = doc.splitTextToSize(overallCommentContent, 540);
  doc.text(splitoverallCommentContent,marginLeft,40)

  doc.addPage();
  

  let foodAverageScore = 0
  for(let [k,v] of Object.entries(props.vipreceiptdetails)){

    let headers = [[v.ItemName2, ""]];
    
    let t = {
      food: [  
      ]
    }

    let total =0 ;
    for(let [key,res] of Object.entries(v.Comment.q1)){
      let curQ = key[3]     
      t.food.push({question:SurveyTemplate.foodQuestion[curQ] , mark: res})
      total += parseInt(res)
      
    }
    foodAverageScore += total
    
    t.food.push({question:"", mark: total})
    const data = t.food.map(elt=> [elt.question, elt.mark]);

    let tableStartY = 20;
    if(v.Photo ){
      doc.addImage( process.env.REACT_APP_API_SERVER_ADDRESS + v.Photo.url, 'JPEG', 207.5,  20, 180, 160);
      tableStartY=200;
    }

    let content = {
      startY: tableStartY,
      head: headers,
      body: data,
      styles: { font: 'GenJyuuGothic-Light' },
      didParseCell : function(data) {      
          if (data.row.index == 6 )
            data.cell.styles.font  = 'GenJyuuGothic-Bold'
      },
      columnStyles: {
        0: {
          cellWidth: 'auto'
        },
        1: {
          cellWidth: 50
        }
      }
      
    };
    doc.autoTable(content);
    
    if(v.Comment.q3){
      let overallFoodComment = (': ').concat(v.Comment.q3);
      var splitTitle = doc.splitTextToSize(overallFoodComment, 540);
      doc.text(splitTitle, marginLeft, doc.lastAutoTable.finalY+50);
    }
    else{
      if(v.Comment.q2){
        let overallFoodComment = (': ').concat('');
        var splitTitle = doc.splitTextToSize(overallFoodComment, 540);
        doc.text(splitTitle, marginLeft, doc.lastAutoTable.finalY+50);
      }
    }
    // if(v.Comment.q2){
    //   let overallFoodComment = (': ').concat(v.Comment.q2);
    //   doc.text( overallFoodComment, marginLeft, doc.lastAutoTable.finalY+50);
    
    // }
    // const imgData = 'data:image/jpeg;base64,'+ Base64.encode('http://192.168.1.171:1337/uploads/6046022cbc84e73135b54c9f_IMG_9380_Shop_Front_1_2152546cbf.jpg');
    doc.addPage();   
  }
  foodAverageScore /= props.vipreceiptdetails.length
  Score.push(foodAverageScore)


  doc.text("", marginLeft, 40);
  headers = [[ "","","",'']];
    
    let t = {
      resultTable: [  
      ]
    }

  let total =0 ;    
  let tmp;

    for(let i=1;i<=Score.length;i++){

      if (i==8)
        tmp = ResultTable.get('p2')
      else
        tmp = ResultTable.get( i.toString())

      t.resultTable.push({question: tmp.question, mark: parseFloat(Score[i-1]).toFixed(1), weight: (tmp.weight*100+'%'), weightScore: parseFloat((tmp.weight * Score[i-1]/tmp.total*100)).toFixed(1) })
      total += parseInt( tmp.weight * Score[i-1]/tmp.total*100)
    
  }   

  t.resultTable.push({question:'',mark:'',weight:'',weightScore:(total+'/100')})
  
  

  const data = t.resultTable.map(elt=> [elt.question, elt.mark,elt.weight,elt.weightScore]);
  let content = {
    startY: 80,
    head: headers,
    body: data,
    styles: { font: 'GenJyuuGothic-Light' },
    didParseCell : function(data) {      
        if (data.row.index == 8 )
          data.cell.styles.font  = 'GenJyuuGothic-Bold'
    },
    columnStyles: {
      0: {
        cellWidth: 'auto'
      },
      1: {
        cellWidth: 70
      },
      2: {
        cellWidth: 70
      },
      3: {
        cellWidth: 70
      }
    }
    
  };
  doc.autoTable(content);


  // content = {
  //   startY: 50,
  //   head: headers,
  //   body: data,
  //   startY: doc.lastAutoTable.finalY + 20,
  //   styles: { font: 'GenJyuuGothic-Light' }
    
  // };
  // doc.autoTable(content);

  // doc.addPage();
  //   // add the font to jsPDF
  // const imgData = 'data:image/jpeg;base64,'+ Base64.encode('http://192.168.1.171:1337/uploads/6046022cbc84e73135b54c9f_IMG_9380_Shop_Front_1_2152546cbf.jpg');
  // doc.addImage('http://192.168.1.171:1337/uploads/6046022cbc84e73135b54c9f_IMG_9380_Shop_Front_1_2152546cbf.jpg', 'JPEG', 15, 250, 180, 160);
  
  
  doc.save( filename + 'report.pdf')
}
