export const ISO8601ToDate = (date) => {
    let tmp = parseISOLocal(date)

    let d = Date.parse(tmp)
    d = new Date(d)
    
    let month = d.getMonth()+1, day = d.getDate();

    if(d.getMonth()<10)
      month = '0' + (d.getMonth()+1)
    
    if(d.getDate()<10)
      day = '0' + (d.getDate())

    return d.getFullYear()+'-' + (month) + '-'+ day ;

}
export const ISO8601ToTime = (time) => {


    let d = Date.parse(time)


    d = new Date(d)
    d=(d.toUTCString())

    time = d.toString().split(' ')

    return time[4]

}

const parseISOLocal = (s)  => {
var b = s.split(/\D/);
    
return new Date(b[0], b[1]-1, b[2], b[3], b[4], b[5]);
}


