import React, { useEffect,useState } from "react";
import {useParams,useHistory}  from "react-router-dom";


function setToken(userToken:String, username:String) {
    sessionStorage.setItem('token', userToken);
    sessionStorage.setItem('username', username );  
    

    
    
  }

  
// function getToken() {
//     const tokenString = sessionStorage.getItem('token');
//     const userToken = JSON.parse(tokenString);

//     return userToken;
// }

export default function SignIn(props) {
    
    const [correct,setCorrect] = useState(true)
    let history = useHistory()

    useEffect(()=>{
        var myHeaders = new Headers(); 
        myHeaders.append("Content-Type", "application/json"); 

        var raw = JSON.stringify({"username":props.location.state.username,"autologinpass":props.location.state.id});
     

        var requestOptions = {
        method: 'POST',
        headers: myHeaders,
        body: raw,
        redirect: 'follow'
        };

        fetch( process.env.REACT_APP_API_SERVER_ADDRESS+"/auth/auto", requestOptions)
        .then(response => response.text())
        .then(result => {
           
            if (result){
                const tmp = JSON.parse(result)
                console.log(tmp)
                const statusCode = tmp['statusCode'];

                console.log(statusCode)                 
                if(statusCode=='400' || statusCode=='404' || statusCode=='403'){
                    setCorrect(false)                    
                } else{
                    setToken(tmp['jwt'],props.location.state.username)                   
                    props.parentCallBack();      
                    let path = '/menu';
                    
                    history.push({
                        pathname: path                        
                    });          
                    
                    
                }
            }
            // else{
            //     setCorrect(false);
            // }
        })
        .catch(error => console.log('error', error));

            })
       
        return (
            correct ? <div>Redirecting To ...... </div> : <div>Your QR code is not validated</div>
         )

}
